<template>
  <div>
    <Head title="Add Payment Method" />
    <h1 class="mb-8 text-3xl font-bold">
      <InertiaLink class="text-indigo-400 hover:text-indigo-600" :href="route('admin.payment-methods.index')">Payment Methods</InertiaLink>
      <span class="text-indigo-400 font-medium">/</span> Create
    </h1>
    <div class="max-w-3xl bg-white rounded-md shadow overflow-hidden">
      <form @submit.prevent="store">
        <div class="flex flex-wrap -mb-8 -mr-6 p-8">
          <text-input v-model="form.account_owner" :error="form.errors.account_owner" class="pb-8 pr-6 w-full lg:w-1/2" label="Account Owner Name" />
          <text-input v-model="form.account_no" :error="form.errors.account_no" class="pb-8 pr-6 w-full lg:w-1/2" label="Account Number" />
          <text-input v-model="form.name" :error="form.errors.name" class="pb-8 pr-6 w-full lg:w-1/2" label="Payment Provider Name" />
          <file-input v-model="form.image" :error="form.errors.image" class="pb-8 pr-6 w-full lg:w-1/2" label="Payment Image" />
        </div>
        <div class="flex items-center justify-end px-8 py-4 bg-gray-50 border-t border-gray-100">
          <loading-button :loading="form.processing" class="btn-indigo" type="submit">Submit</loading-button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { Head, InertiaLink } from '@inertiajs/inertia-vue3'
import Layout from '@/Layouts/AdminLayout.vue'
import TextInput from '@/Shared/TextInput'
import LoadingButton from '@/Shared/LoadingButton'
import FileInput from '@/Shared/FileInput'

export default {
  components: {
    Head,
    InertiaLink,
    TextInput,
    LoadingButton,
    FileInput
  },
  layout: Layout,
  data() {
    return {
      form: this.$inertia.form({
        account_owner: '',
        account_no: '',
        name: '',
        image: null
      }),
    }
  },
  methods: {
    store() {
      this.form.post(this.route('admin.payment-methods.store'));
    },
  }
}
</script>
