<template>
  <div>
    <Head title="Products" />
    <h1 class="mb-8 text-3xl font-bold">Products</h1>
    <div class="flex items-center justify-between mb-6">
      <search-filter v-model="form.search" class="mr-4 w-full max-w-md" @reset="reset">
        <label class="block text-gray-700">Trashed:</label>
        <select v-model="form.trashed" class="form-select mt-1 w-full">
          <option :value="null" />
          <option value="with">With Trashed</option>
          <option value="only">Only Trashed</option>
        </select>
      </search-filter>
      <InertiaLink class="btn-indigo" :href="route('admin.products.create')">
        <span>Add</span>
      </InertiaLink>
    </div>
    <section>
      <div class="container mx-auto">
        <div class="grid gap-4 grid-cols-2 sm:grid-cols-3 lg:grid-cols-4">
          <ProductCard />
          <ProductCard />
          <ProductCard />
          <ProductCard />
          <ProductCard />
          <ProductCard />
          <ProductCard />
          <ProductCard />
          <ProductCard />
        </div>
      </div>
    </section>
    <button data-modal-target="popup-modal" data-modal-toggle="popup-modal" class="bg-blue-700 hover:bg-blue-800 focus:ring-blue-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800 block px-5 py-2.5 text-center text-white text-sm font-medium rounded-lg focus:outline-none focus:ring-4" type="button">Toggle modal</button>
    <ItemAddModal />
  </div>
</template>

<script>
import { Head, InertiaLink } from '@inertiajs/inertia-vue3'
import Layout from '@/Layouts/AdminLayout.vue'
import pickBy from 'lodash/pickBy'
import throttle from 'lodash/throttle'
import mapValues from 'lodash/mapValues'
import SearchFilter from '@/Shared/SearchFilter'
import ProductCard from '../Components/ProductCard.vue'
import ItemAddModal from '../Components/Admin/ItemAddModal.vue'

export default {
  components: {
    Head,
    InertiaLink,
    SearchFilter,
    pickBy,
    ProductCard,
    ItemAddModal,
  },
  layout: Layout,
  props: {
    filters: Object,
    contacts: Object,
  },
  data() {
    return {
      showItemAddModal: false,
      form: {
        search: this.filters.search,
        trashed: this.filters.trashed,
      },
    }
  },
  watch: {
    form: {
      deep: true,
      handler: throttle(function () {
        this.$inertia.get(this.route('admin.products.index'), pickBy(this.form), { preserveState: true })
      }, 150),
    },
  },
  methods: {
    reset() {
      this.form = mapValues(this.form, () => null)
    },
  },
}
</script>
